html,
body,
#root {
  height: 100vh;
  width: 100vw;
  margin: 0;
  padding: 0;
  background: #1d1d1d;
}

/* Fonts */
/* font-family: 'Work Sans', sans-serif; */
/* font-family: 'Source Code Pro', monospace; */
/* #ff2253 */

.name_container {
  position: fixed;
  width: 100vw;
  height: 100vh;
}

.name_svg {
  position: relative;
  height: 25rem;  /* Create media query for mobile */
  width: 25rem;   /* Create media query for mobile */
  margin: auto;
  top: 20%;
}


.name_line {
  position: relative;
  bottom: 5rem;
}

.job_title {
  position: relative;
  height: 30rem;  /* Create media query for mobile */
  bottom: 36rem;
}

.enter_btn {
  position: absolute;
    top: 350px;
    right: 150px;
    color: #5f5f5f;
    background: #242424;
    border: 1px solid #444444;
    padding: 1rem 3rem;
    font-size: 1rem;
    box-shadow: 0px 0px 3px #08fdd8;
    border-radius: 30px;
    cursor: pointer;
    opacity: 0;
    text-transform: uppercase;
    text-decoration: none;
} 

.enter_btn:hover {
  box-shadow: 0px 0px 5px rgba(8, 253, 216, 1);
  color: #5f5f5f;
}

.enter_btn {
  animation: fadeIn 5s linear 5s forwards;
}

@keyframes fadeIn {
  0% { opacity:0; }
  50% { opacity:.5; } 
  100% { opacity:1; } 
 }


.sidebar_container {
  position: absolute;
  grid-area: sidebar;
  height: 100vh;
}



.sidebar_container {
  display: flex;

  flex-direction: column;
  padding: 5rem 0rem;
  height: 100vh;
  width: 15%;
  background-color: #242424 !important;
  align-items: center;
  font-family: 'Antic Slab';
  font-size: 1.2rem;
  box-shadow: inset 0 -2px 8px rgb(0 0 0 / 44%);
}

.sidebar_container > .initials {
  font-family: 'Work Sans', sans-serif;
  font-weight: 300;
  font-size: 10rem;
  color: rgb(8, 253, 216);
  border-bottom: 1px solid #5f5f5f;
  border-top: 1px solid #5f5f5f;
  border-radius: 40px;
  cursor: default;
}

@media screen and (max-width:1400px){ 
  .sidebar_container > .initials {
    font-size: 7rem;
    border-radius: 30px;
  }
  .sidebar_container h3 {
    font-size: 1rem;
    margin-top: 4px;
    text-align: center;
  }
  .sidebar_container .sidebar_links {
    font-size: 1rem;
  }
}

@media screen and (max-width:1170px){ 
  .sidebar_container > .initials {
    font-size: 5.5rem;
    border-radius: 30px;
  }
  .sidebar_container h3 {
    font-size: .85rem;
    margin-top: 4px;
    text-align: center;
  }
  .sidebar_container .sidebar_links {
    font-size: .85rem;
  }
}
@media screen and (max-width:700px){ 
  .name_svg {
    width: 21rem;
  }
  .name_line {
    bottom: 7rem !important;
  }

  .enter_btn {
    right: 30%;
  }

  .sidebar_container > .initials {
    font-size: 3rem;
    border-radius: 10px;
  }
  .sidebar_container h3 {
    font-size: .73rem;
    text-align: center;
    margin-top: 4px;
  }
  .sidebar_container .sidebar_links {
    font-size: .75rem;
  }
}

.sidebar_container h3 {
  color: #5f5f5f !important;
  text-align: center;
}

.sidebar_container .sidebar_links {
  position: relative;
  display: flex;
  flex-direction: column;
  top: 10px;
  cursor: pointer;
  width: 100%;
  color: #5f5f5f;
}


.sidebar_container .sidebar_links a {
  text-align: center;
  padding: 1rem 0;
  border-bottom: 1px solid #5f5f5f;
  cursor: pointer;
  transition: all 1s;
  text-decoration: none;
  color: inherit;
}

.sidebar_container .sidebar_links a:hover {
  color:rgba(255, 255, 255, .7);
  border-bottom: 1px solid rgba(255, 255, 255, .7);
}


.projects_container {
  height: 100%;
  width: 100%;
  grid-area: project;
  overflow-y: auto;
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}
/* Hide scrollbar for Chrome, Safari and Opera */
.projects_container::-webkit-scrollbar {
  display: none;
}


.projects {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 1fr;
  grid-column-gap: 5px;
  grid-row-gap: 5px;
  align-items:center;
  padding: 3rem;
  height: 100%;
  width: 100%;
  grid-template-areas: "card_1 card_2 card_3";
  justify-content: center;
  justify-items: center;
}

.projects .cards:nth-of-type(1) {
  grid-area: card_1;
}
.projects .cards:nth-of-type(2) {
  grid-area: card_2;
}
.projects .cards:nth-of-type(3) {
  grid-area: card_3;
}
.cards {
  min-height: 400px;
  width: 300px !important;
  border: 1px solid #242424 !important;
  background: linear-gradient(
    85deg
    ,#434343,#262626) !important;
  cursor: default !important;
} 

.cards:hover .ant-card-cover {
  transform: scale(1.3) translateY(-20px);
  transition: all .3s;
}

/* Projects Section */

.projects_container > h1 {
  position: relative;
  font-family: "Roboto";
  font-weight: 800;
  top: 10%;
  font-size: 2rem;
  text-align: center;
  color: rgb(8, 253, 216);
  text-transform: uppercase;
  }
  .projects_container > h3 {
  position: relative;
  font-family: "Roboto";
  top: 10%;
  font-size: 1rem;
  text-align: center;
  color: rgba(255, 255, 255, .7);
  text-transform: uppercase;
  
  }
  
  .ant-card-meta-title {
    color:#3268a5!important;
    font-family: "Work Sans";
    font-size: 1.1rem !important;
  }
  
  .ant-card-meta-description {
    color: rgba(255, 255, 255, .9) !important;
    font-family: "Work Sans";
  }
  
  .box .ball, .box .ball_two, .ball_three {
      width: 40px;
      height: 40px;
      border-radius: 50%;
      background-color: #ff2253;
      box-shadow: inset 5px 5px 5px rgba(0,0,0,.6), 15px 15px 2px rgba(0,0,0,.04);
      position: absolute;
    }
    
    .ball {
        animation: moveX 6s linear 0s infinite alternate, moveY 6s linear 0s infinite alternate;
    }
    
    .ball_two {
        animation: moveX_two 6s linear 0s infinite alternate, moveY_two 5s linear 0s infinite alternate;
    }
    
    .ball_three {
        animation: moveX_three 6s linear 0s infinite alternate, moveY_three 7s linear 0s infinite alternate;
    }
    
    @keyframes moveX {
      from { left: 100%; } to { left: 0; }
    }
    
    @keyframes moveY {
      from { top: 0; } to { top: 100vh; }
    }
    
    @keyframes moveX_two {
      from { left: 0vw; } to { left: 100vh; }
    }
    
    @keyframes moveY_two {
      from { top: 0; } to { top: 100vh; }
    }
    
    @keyframes moveX_three {
      from { left: 0vw; } to { left: 100%; }
    }
    
    @keyframes moveY_three {
      from { top: 100vh; } to { top: 0; }
    }
  
    @media screen and (max-width:1165px){ 
      .projects {
        display: grid;
        position: relative;
        top: 21vh;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: 1fr 1fr;
        padding: 3rem;
        grid-template-areas: "card_1 card_2"
                             "card_3 .";
      }
    }
    
    @media screen and (max-width:800px){ 
      .projects {
        display: grid;
        position: relative;
        grid-template-columns: 1fr;
        grid-template-rows: 1fr 1fr 1fr;
        padding: 3rem;
        grid-template-areas:
        "card_1"
        "card_2"
        "card_3";
      }
      .projects_container > h1 {
        font-size: 1.5rem;
        }
        .projects_container > h3 {
        font-size: .9rem;
        }
    }

/* Skills Sections */

.skills_container {
  background-color: #1d1d1d;
  height: 100%;
  width: 100%;
  grid-area: project;
}


.main_container {
  display: grid;
  grid-template-columns: 15% 1fr;
  grid-template-rows: 1fr;
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  height: 100vh;
  width: 100vw;
  grid-template-areas: "sidebar project";
}

.github-corner > svg {
  z-index: 10000;
}


.github-corner:hover .octo-arm{animation:octocat-wave 560ms ease-in-out}

@keyframes octocat-wave{0%,100%{transform:rotate(0)}20%,60%{transform:rotate(-25deg)}40%,80%{transform:rotate(10deg)}}

@media (max-width:500px){.github-corner:hover .octo-arm{animation:none}.github-corner .octo-arm{animation:octocat-wave 560ms ease-in-out}}

.work_container {
  display:flex;
  height: 100%;
  width: 100%;
  justify-content: center;
  align-items: center;
}

.ant-timeline {
  background-color: #1d1d1d;
  width: 30vw;
}

.ant-timeline-item-tail {
  border-left: 2px solid #08fdd8 !important;
}

.ant-timeline-item-head, .ant-timeline-item-head-blue {
  border-color: #242424 !important;
}

.ant-timeline-item-content {
  color: rgba(255, 255, 255, .9);
  font-family: 'Roboto', sans-serif;
  letter-spacing: .5px;
}

.ant-timeline-item-content:hover {
  color:  rgba(255, 34, 83, .7);
  cursor:default;
}

.work {
  position: absolute;
  font-family: 'Source Code Pro', monospace;
  font-style: italic;
  font-size: 4rem;
}

/* Media Queries for Skills & Work Section*/
@media screen and (max-width:1550px){ 
  .work {
    font-size: 3rem;
  }
}
@media screen and (max-width:1100px){ 
  .work {
    font-size: 2rem;
  }
  .ant-timeline-item-content {
    font-size: .79rem;
  }
}
@media screen and (max-width:800px){ 
  .work {
    font-size: .9rem;
  }
  .ant-timeline-item-content {
    font-size: .59rem;
    font-family: 'Work Sans';
  }
}

@media screen and (max-width:600px){ 
  .work {
    font-size: .8rem;
  }
  .ant-timeline-item-content {
    font-size: .48rem;
    letter-spacing: .8px;
    font-weight: 100;
    height: 35px;
    font-family: 'Work Sans';
  }
}



